export function bulkUpdateRecipientTags(
  httpClient: HttpClient
): BulkUpdateRecipientTagsSignature {
  return (recipientIds: string[], options?: BulkUpdateRecipientTagsOptions) =>
    universalBulkUpdateRecipientTags(
      recipientIds,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface BulkUpdateRecipientTagsSignature {
  /**
   * Updates tags on multiple recipients by recipient IDs.
   *
   * This is a synchronous operation that updates up to 100 recipients at once.
   * If a tag appears in both assign and unassign lists, it will be assigned.
   * @param - IDs of recipients to update tags for.
   */
  (recipientIds: string[], options?: BulkUpdateRecipientTagsOptions): Promise<
    NonNullablePaths<
      BulkUpdateRecipientTagsResponse,
      | `results`
      | `results.${number}.itemMetadata.originalIndex`
      | `results.${number}.itemMetadata.success`
      | `results.${number}.itemMetadata.error.code`
      | `results.${number}.itemMetadata.error.description`
      | `bulkActionMetadata.totalSuccesses`
      | `bulkActionMetadata.totalFailures`
      | `bulkActionMetadata.undetailedFailures`,
      6
    > & {
      __applicationErrorsType?: BulkUpdateRecipientTagsApplicationErrors;
    }
  >;
}

export function bulkUpdateRecipientTagsByFilter(
  httpClient: HttpClient
): BulkUpdateRecipientTagsByFilterSignature {
  return (
    filter: Record<string, any>,
    options?: BulkUpdateRecipientTagsByFilterOptions
  ) =>
    universalBulkUpdateRecipientTagsByFilter(
      filter,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface BulkUpdateRecipientTagsByFilterSignature {
  /**
   * Updates tags on multiple recipients using filter criteria.
   *
   * This is an asynchronous operation that returns a job ID for tracking progress.
   * An empty filter will update all recipients. If a tag appears in both assign and unassign lists, it will be assigned.
   * @param - Filter.
   */
  (
    filter: Record<string, any>,
    options?: BulkUpdateRecipientTagsByFilterOptions
  ): Promise<
    NonNullablePaths<BulkUpdateRecipientTagsByFilterResponse, `jobId`, 2> & {
      __applicationErrorsType?: BulkUpdateRecipientTagsByFilterApplicationErrors;
    }
  >;
}

export const onRecipientCreated = EventDefinition(
  'wix.restaurants.recipient.v1.recipient_created',
  true,
  (event: RecipientCreatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'entity.createdDate' },
            { path: 'entity.updatedDate' },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<RecipientCreatedEnvelope>();
